import {
  ADICIONAR_USUARIO,
  USUARIOS_LISTAR,
  USUARIOS_LIMPAR,
  ADICIONAR_USUARIOS,
  REMOVER_USUARIO
} from "./actions";

const initialState = {
  usuarios: {}
}

export default (state = initialState, action) => {
  switch (action.type) {

    case ADICIONAR_USUARIO:
      return {
        ...state,
        usuarios: { ...state.usuarios, ...action.usuario }
      }
    case ADICIONAR_USUARIOS:
      return { ...state, usuarios: action.usuarios }
    case REMOVER_USUARIO: {
      const { [action.uid]: valor, ...usuarios } = state.usuarios;
      return { ...state, usuarios };
    }
    case USUARIOS_LISTAR:
      return {
        ...state,
        usuarios: action.usuarios
      }
    case USUARIOS_LIMPAR:
      return {
        ...state,
        usuarios: {}
      }
    default:
      return state
  }
}