export const RODADA_SELECIONADA = "simulador/RODADA_SELECIONADA";
export const ADICIONAR_PLACAR = "simulador/ADICIONAR_PLACAR";
export const ADICIONAR_JOGO_SIMULADOR = "simulador/ADICIONAR_JOGO_SIMULADOR";

export const quandoMudar = time => (dispatch, getState) => {
  dispatch({ type: ADICIONAR_PLACAR, time });
}

export const salvarSimulador = rodada => dispatch => {
  rodada.forEach(jogoSimulador => {
    // dispatch({ type: ADICIONAR_JOGO_SIMULADOR, jogoSimulador });
  });
}

export const buscarRodada = (rodadaId = "1") => (dispatch, getState) => {
  dispatch({ type: RODADA_SELECIONADA, rodadaId });

  const { jogos: jogosGabarito } = getState().admin;
  Object
    .keys(jogosGabarito)
    .filter(jogoId => jogosGabarito[jogoId].rodada === rodadaId)
    .forEach(jogoId => {
      const jogoSimulador = { [jogoId]: { ...jogosGabarito[jogoId], mandante_placar: null, visitante_placar: null } };
      dispatch({ type: ADICIONAR_JOGO_SIMULADOR, jogoSimulador });
    });
};
