import { ENTRAR, SAIR } from "./actions";

const initialState = {
  usuarioLogado: {}
}

export default (state = initialState, action) => {
  switch (action.type) {
    case ENTRAR:
      return { ...state, usuarioLogado: { ...action.usuarioLogado } }
    case SAIR:
      return { ...state, usuarioLogado: {} }
    default:
      return state
  }
}