import firebase from "firebase";
import { persistor } from "../store";
import { adicionarMensagens } from "../app/actions";
import { CARREGANDO_DESLIGADO, ligarCarregando } from "../carregando/actions";
import { ADICIONAR_PALPITE, ADICIONAR_PALPITES, REMOVER_PALPITES } from "../palpite/actions";
import { buscarTodos as buscarUsuarios, ADICIONAR_USUARIOS, REMOVER_USUARIO } from "../usuario/actions";
import { ehAdmin } from "seguranca/actions";
export const RODADA_SELECIONADA = "admin/RODADA_SELECIONADA";
export const RODADA_GABARITO = "admin/RODADA_GABARITO";
export const ADICIONAR_PLACAR = "admin/ADICIONAR_PLACAR";
export const ADICIONAR_JOGO = "admin/ADICIONAR_JOGO";
export const ADICIONAR_JOGOS = "admin/ADICIONAR_JOGOS";
export const ADICIONAR_JOGO_GABARITO = "admin/ADICIONAR_JOGO_GABARITO";
export const ALTERNAR_MODAL_EXCLUIR_USUARIO = "admin/ALTERNAR_MODAL_EXCLUIR_USUARIO";
export const ATUALIZAR_VERIFICARCAO = "admin/ATUALIZAR_VERIFICARCAO";
export const ADICIONAR_CONFIG = "admin/ADICIONAR_CONFIG";

export const base10 = 10;
export const primeiroTurno = 19;
export const MAXIMO_JOGOS_RODADA = 16;

export const verificarConexao = () => (dispatch, getState) => {
  let { ultimaVerificacao, config = {} } = getState().admin;
  const dezMinutosDepois = new Date(Date.now() + (10 * 60 * 1000)).getTime();

  if (!ultimaVerificacao) {
    return persistor.purge()
      .then(retorno => {
        dispatch({ type: ATUALIZAR_VERIFICARCAO, dezMinutosDepois });
        return dispatch(atualizarTudo());
      })
      .then(doc => {
        return window.location.reload();
      });
  }

  if (Date.now() > ultimaVerificacao && config.atualizacaoAutomatica) {
    dispatch({ type: ATUALIZAR_VERIFICARCAO, dezMinutosDepois });
    return firebase.firestore().collection("admin").doc("config")
      .get()
      .then(doc => {
        if (doc.exists && doc.data().sistemaConectado) {
          console.log("Document data:", doc.data());
          return dispatch(atualizarTudo());
        }
      })
      .catch(erros => {
        dispatch(adicionarMensagens(erros));
      });
  }
}

export const buscarConfig = () => dispatch => firebase
  .firestore()
  .collection("admin")
  .doc("config")
  .get()
  .then(doc => {
    if (doc.exists) {
      return dispatch({ type: ADICIONAR_CONFIG, config: doc.data() });
    }
  })
  .catch(erros => {
    dispatch(adicionarMensagens(erros));
  });

export const quandoMudar = time => (dispatch, getState) => {
  if (ehAdmin(getState())) {
    dispatch({ type: ADICIONAR_PLACAR, time });
  }
}

export const enviarTudo = () => (dispatch, getState) => {

  if (ehAdmin(getState())) {
    dispatch(ligarCarregando());
    const {
      palpite: { palpites },
      usuario: { usuarios: usuariosCache },
      admin: { jogos: jogosCache }
    } = getState();

    // const config = JSON.stringify(configCache);
    const palpitesPrimeiroTurno = JSON.stringify(palpites.filter(p => parseInt(p.rodada, base10) <= primeiroTurno));
    const palpitesSegundoTurno = JSON.stringify(palpites.filter(p => parseInt(p.rodada, base10) > primeiroTurno));
    const usuarios = JSON.stringify(usuariosCache);
    const jogos = JSON.stringify(jogosCache);

    const batch = firebase.firestore().batch();

    // const configDocRef = firebase.firestore().collection("admin").doc("config");
    // batch.set(configDocRef, { config });

    const palpitesPrimeiroTurnoDocRef = firebase.firestore().collection("admin").doc("palpitesPrimeiroTurno");
    batch.set(palpitesPrimeiroTurnoDocRef, { palpitesPrimeiroTurno });

    const palpitesSegundoTurnoDocRef = firebase.firestore().collection("admin").doc("palpitesSegundoTurno");
    batch.set(palpitesSegundoTurnoDocRef, { palpitesSegundoTurno });

    const usuariosDocRef = firebase.firestore().collection("admin").doc("usuarios");
    batch.set(usuariosDocRef, { usuarios });

    const jogosDocRef = firebase.firestore().collection("admin").doc("jogos");
    batch.set(jogosDocRef, { jogos });

    //dispatch({ type: ADICIONAR_PALPITE, palpite });

    batch.commit()
      .then(() => {
        dispatch(adicionarMensagens("Dados Enviados em Lote com Sucesso!"));
      })
      .catch(erros => {
        dispatch(adicionarMensagens(erros));
      });
  }
}

export const atualizarTudo = () => (dispatch, getState) => {

  if (!ehAdmin(getState())) {
    dispatch(ligarCarregando());
    return firebase.firestore().collection("admin").get()
      .then(function (querySnapshot) {
        let palpites = [];
        querySnapshot.forEach(doc => {
          switch (doc.id) {
            case "jogos":
              dispatch({ type: ADICIONAR_JOGOS, jogos: JSON.parse(doc.data()[doc.id]) })
              break;
            case "palpitesPrimeiroTurno":
            case "palpitesSegundoTurno":
              palpites = [...palpites, ...JSON.parse(doc.data()[doc.id])]
              dispatch({ type: ADICIONAR_PALPITES, palpites })
              break;
            case "usuarios":
              dispatch({ type: ADICIONAR_USUARIOS, usuarios: JSON.parse(doc.data()[doc.id]) })
              break;
            case "config":
              const config = doc.data();
              dispatch({ type: ADICIONAR_CONFIG, config });
              break;
            default:
              dispatch(adicionarMensagens("Erro: Dados inesperados foram retornados do servidor!"));
              break;
          }
        });
        dispatch(adicionarMensagens("Dados Atualizados em Lote com Sucesso!"));
        return Promise.resolve("OK");
      })
      .catch(erros => {
        return Promise.reject(erros);
      });
  }

  const forcarAtualizacao = true;
  return Promise.all([dispatch(buscarUsuarios(forcarAtualizacao)), dispatch(buscarRodadas(forcarAtualizacao))]);
}

export const buscarRodadas = (forcarAtualizar = false) => dispatch => {
  dispatch(ligarCarregando());

  if (forcarAtualizar) {

    return Promise.all([firebase.firestore().collection("jogos").get(), firebase.firestore().collection("palpites").get()])
      .then(([querySnapshotJogos, querySnapshotPalpites]) => {
        if (!querySnapshotJogos.empty) {
          let jogos = {};
          querySnapshotJogos.forEach(doc => {
            const jogo = { [doc.id]: { ...doc.data() } };
            jogos = { ...jogos, ...jogo };
          });
          dispatch({ type: ADICIONAR_JOGOS, jogos })
        }
        if (!querySnapshotPalpites.empty) {
          let palpites = [];
          querySnapshotPalpites.forEach(doc => {
            const palpite = { id: doc.id, ...doc.data() };
            palpites.push(palpite);
          });
          dispatch({ type: ADICIONAR_PALPITES, palpites })
        }
        dispatch({ type: CARREGANDO_DESLIGADO });
        return Promise.resolve("");
      })
      .catch(erros => {
        dispatch(adicionarMensagens(erros));
        return Promise.reject(erros);
      });
  }
}

export const atualizar = (rodadaId, usuarioId) => dispatch => {
  dispatch(ligarCarregando());

  Promise.all([
    firebase.firestore().collection("jogos").where("rodada", "==", rodadaId).get(),
    usuarioId
      ? firebase.firestore().collection("palpites").where("usuarioId", "==", usuarioId).where("rodada", "==", rodadaId).get()
      : Promise.resolve({ empty: true })
  ])
    .then(([querySnapshotJogos, querySnapshotPalpites]) => {
      if (!querySnapshotJogos.empty) {
        querySnapshotJogos.forEach(doc => {
          const jogo = { [doc.id]: { ...doc.data() } };
          dispatch({ type: ADICIONAR_JOGO, jogo })
        });
      }
      if (!querySnapshotPalpites.empty) {
        querySnapshotPalpites.forEach(doc => {
          const palpite = { id: doc.id, ...doc.data() };
          dispatch({ type: ADICIONAR_PALPITE, palpite })
        });
      }
      dispatch(adicionarMensagens(`Gabarito e Jogos da Rodada ${rodadaId} atualizados com sucesso!`));
      dispatch({ type: CARREGANDO_DESLIGADO });
    })
    .catch(erros => {
      dispatch(adicionarMensagens(erros));
    });
}

export const buscarRodada = (rodadaId = "1") => (dispatch, getState) => {
  dispatch(ligarCarregando());
  dispatch({ type: RODADA_SELECIONADA, rodadaId });

  const jogos = getState().admin.jogos;
  const rodadaGabarito = Object.keys(jogos)
    .filter(jogoId => jogos[jogoId].rodada === rodadaId)
    .map(jogoId => ({ jogoId, ...jogos[jogoId] }));

  if (rodadaGabarito.length === MAXIMO_JOGOS_RODADA) {
    dispatch({ type: CARREGANDO_DESLIGADO });
    return Promise.resolve(`Gabarito da rodada ${rodadaId} em cache`);
  }

  return firebase.firestore()
    .collection("jogos")
    .where("rodada", "==", rodadaId)
    .get()
    .then(querySnapshot => {
      if (!querySnapshot.empty) {
        querySnapshot.forEach(doc => {
          const jogo = { [doc.id]: { ...doc.data() } };
          dispatch({ type: ADICIONAR_JOGO, jogo })
        });
        dispatch({ type: CARREGANDO_DESLIGADO });
        return Promise.resolve(`Gabarito da rodada ${rodadaId} remota em cache`);
      }
      return Promise.reject("O Administrador ainda no cadastrou essa rodada");
    })
    .catch(erros => {
      dispatch(adicionarMensagens(erros));
      return Promise.reject(erros)
    });
}

export const buscarGabarito = (rodadaId = "1") => (dispatch, getState) => {
  dispatch(ligarCarregando());
  dispatch({ type: RODADA_SELECIONADA, rodadaId });

  if (ehAdmin(getState())) {
    let {
      config: {
        campeonato = {}
      } = {}
    } = getState().admin;
    if (campeonato.nome === "Bolo da Copa") {
      buscarGabaritoCopa(rodadaId, campeonato.jogosPorRodada)
        .then(rodadaGabarito => {
          Object.keys(rodadaGabarito).forEach(jogoId => {
            const jogoGabarito = { [jogoId]: { ...rodadaGabarito[jogoId] } };
            dispatch({ type: ADICIONAR_JOGO_GABARITO, jogoGabarito })
          });
          dispatch({ type: CARREGANDO_DESLIGADO });
          console.log(`Gabarito da rodada ${rodadaId} GloboEsporte em cache`);
        })
        .catch(erros => dispatch(adicionarMensagens(erros)));;
    }
    if (campeonato.nome === "Brasileiro Srie A") {
      buscarGabaritoBrasileiroA(rodadaId, campeonato.jogosPorRodada)
        .then(rodadaGabarito => {
          Object.keys(rodadaGabarito).forEach(jogoId => {
            const jogoGabarito = { [jogoId]: { ...rodadaGabarito[jogoId] } };
            dispatch({ type: ADICIONAR_JOGO_GABARITO, jogoGabarito })
          });
          dispatch({ type: CARREGANDO_DESLIGADO });
          console.log(`Gabarito da rodada ${rodadaId} GloboEsporte em cache`);
        });
    }
  }

  dispatch({ type: CARREGANDO_DESLIGADO });
  dispatch(buscarRodada(rodadaId))
    .then(retorno => dispatch(adicionarMensagens(retorno)))
    .catch(erros => dispatch(adicionarMensagens(erros)));
}

export const salvarRodada = rodada => dispatch => {
  dispatch(ligarCarregando());

  const batch = firebase.firestore().batch();

  const jogos = rodada.map(jogo => {
    const jogoDocRef = firebase.firestore().collection("jogos").doc(jogo.jogoId);
    // const { mandante_escudo: me, visitante_escudo: ve } = jogo;
    // const mandante_escudo = `${process.env.PUBLIC_URL}/escudos/${me.replace(/.*\//, "")}`;
    // const visitante_escudo = `${process.env.PUBLIC_URL}/escudos/${ve.replace(/.*\//, "")}`;
    // const jogoAtualizado = { ...jogo, mandante_escudo, visitante_escudo };
    const jogoAtualizado = { ...jogo };
    batch.set(jogoDocRef, { ...jogoAtualizado });
    return jogoAtualizado;
  });

  batch.commit()
    .then(_ => {
      jogos.forEach(jogo => dispatch({ type: ADICIONAR_JOGO, jogo: { [jogo.jogoId]: { ...jogo } } }));

      console.log("Transaction successfully committed!");
      dispatch({ type: CARREGANDO_DESLIGADO });
      dispatch(adicionarMensagens("Rodada salva com sucesso!"));
    })
    .catch(erros => {
      dispatch(adicionarMensagens(erros));
    });
}

const buscarGabaritoBrasileiroA = (rodadaId = "1", jogosPorRodada) => {
  const headers = new Headers();
  const base_url = "https://globoesporte.globo.com/";
  const proxy_url = "https://cors-anywhere.herokuapp.com/";
  const caminho = `servico/esportes_campeonato/responsivo/widget-uuid/1d8d8f08-d07c-4921-b35b-17701b1b73e5/fases/fase-unica-seriea-2018/rodada/${rodadaId}/jogos.html`;

  var myInit = {
    method: "GET",
    headers: headers,
  };

  return fetch(proxy_url + base_url + caminho, myInit)
    .then(r => r.ok ? r.text() : Promise.reject(r))
    .then(html => {
      const rodadaHtml = new DOMParser().parseFromString(html, "text/html");

      const rodadaJson = JSON.parse([...rodadaHtml.querySelectorAll(".placar-jogo")]
        .map(jogo => [...jogo.querySelectorAll(".placar-jogo-informacoes, .placar-jogo-equipes-escudo-visitante, .placar-jogo-equipes-escudo-mandante, .placar-jogo-equipes-nome, .placar-jogo-equipes-placar-mandante, .placar-jogo-equipes-placar-visitante")])
        .reduce((acumulador, time, j) => {
          let jogo = JSON.parse(time.reduce((anterior, atual, i) => {
            if (i === 0) {
              const [, data = "", local = "", hora = ""] = atual.textContent.match(/^.*(\d{2}\/\d{2}\/\d{4})\s(.*)\s(\d{2}:\d{2})$/) || [];
              return anterior + `{"data":"${data}","hora":"${hora}","local":"${local}",`;
            }
            if (i === 1) return anterior + `"mandante_nome":"${atual.textContent}",`;
            if (i === 2) return anterior + `"mandante_escudo":"${atual.src}",`;
            if (i === 3) return anterior + `"mandante_placar":${atual.textContent || null},`;
            if (i === 4) return anterior + `"visitante_placar":${atual.textContent || null},`;
            if (i === 5) return anterior + `"visitante_escudo":"${atual.src}",`;
            if (i === 6) return anterior + `"visitante_nome":"${atual.textContent}","rodada": "${rodadaId}"}`;
            return "";
          }, ""));
          return `${acumulador}"${window.btoa(`${jogo.mandante_nome}_${jogo.visitante_nome}_${rodadaId}`)}":${JSON.stringify(jogo)}${j === jogosPorRodada - 1 ? "}" : ","}`
        }, "{"));

      return Promise.resolve(rodadaJson);
    })
    .catch(erros => Promise.reject(erros));
}

const buscarGabaritoCopa = (rodadaId = "1") => {
  const headers = new Headers();
  const base_url = "http://globoesporte.globo.com/";
  const proxy_url = "https://cors-proxy.htmldriven.com/?url=";
  const caminho = `servico/esportes_campeonato/responsivo/widget-uuid/cdec28af-302d-4a52-bcdc-283f6d63ea61/fases/fase-grupos-copa-do-mundo-2018/rodada/${rodadaId}/jogos.html`;

  var myInit = {
    method: "GET",
    headers: headers,
  };

  return fetch(proxy_url + base_url + caminho, myInit)
    .then(r => r.ok ? r.text() : Promise.reject(r))
    .then(html => {
      const rodadaHtml = new DOMParser().parseFromString(JSON.parse(html).body, "text/html");

      const rodadaJson = JSON.parse([...rodadaHtml.querySelectorAll(".placar-jogo-equipes")]
        .map(jogo => [...jogo.querySelectorAll(".placar-jogo-equipes-escudo-visitante, .placar-jogo-equipes-escudo-mandante, .placar-jogo-equipes-nome, .placar-jogo-equipes-placar-mandante, .placar-jogo-equipes-placar-visitante")])
        .reduce((acumulador, time, j) => {
          let jogo = JSON.parse(time.reduce((anterior, atual, i) => {
            if (i === 0) return anterior + `{"mandante_nome":"${atual.textContent}",`;
            if (i === 1) return anterior + `"mandante_escudo":"${atual.src}",`;
            if (i === 2) return anterior + `"mandante_placar":${atual.textContent || null},`;
            if (i === 3) return anterior + `"visitante_placar":${atual.textContent || null},`;
            if (i === 4) return anterior + `"visitante_escudo":"${atual.src}",`;
            if (i === 5) return anterior + `"visitante_nome":"${atual.textContent}","rodada": "${rodadaId}"}`;
            return "";
          }, ""));
          return `${acumulador}"${window.btoa(`${jogo.mandante_nome}_${jogo.visitante_nome}_${rodadaId}`)}":${JSON.stringify(jogo)}${j === 15 ? "}" : ","}`
        }, "{"));

      return Promise.resolve(rodadaJson);
    })
    .catch(erros => Promise.reject(erros));
}

export const removerUsuario = (exclusaoConfirmada = false, usuarioId) => (dispatch, getState) => {
  if (exclusaoConfirmada) {
    dispatch(ligarCarregando());
    const { palpite: { palpites }, admin: { usuarioIdSelecionado: uid } } = getState();

    const batch = firebase.firestore().batch();
    const usuarioDocRef = firebase.firestore().collection("usuarios").doc(uid);
    batch.delete(usuarioDocRef);

    const p = palpites
      .filter(p => p.usuarioId === uid)
      .map(p => {
        const palpiteDocRef = firebase.firestore().collection("palpites").doc(p.id);
        batch.delete(palpiteDocRef);
        return p;
      });

    batch.commit()
      .then(() => {
        dispatch({ type: REMOVER_PALPITES, palpites: p });
        dispatch({ type: REMOVER_USUARIO, uid });
        dispatch({ type: CARREGANDO_DESLIGADO });
        dispatch(adicionarMensagens("Usurio e seus palpites removidos com Sucesso!"));
      })
      .catch(erros => {
        dispatch(adicionarMensagens(erros));
      });

    dispatch({ type: ALTERNAR_MODAL_EXCLUIR_USUARIO });
  }
  else {
    dispatch({ type: ALTERNAR_MODAL_EXCLUIR_USUARIO, usuarioId });
  }
}

export const apagarCache = () => persistor
  .purge()
  .then(retorno => {
    window.location.reload();
  })
  .catch(erros => {
    window.location.reload();
  });