export const CARREGANDO_DESLIGADO = "carregando/CARREGANDO_DESLIGADO";
export const CARREGANDO_LIGADO = "carregando/CARREGANDO_LIGADO";
export const cancelar = () => dispatch => dispatch({ type: CARREGANDO_DESLIGADO });

export const ligarCarregando = () => (dispatch, getState) => {
  !getState().carregando.carregando && dispatch({ type: CARREGANDO_LIGADO });
};