import React from "react";
import {
  Avatar,
  Grid,
  Card,
  CardTitle,
  CardText,
  DataTable,
  TableHeader,
  TableBody,
  TableRow,
  TableColumn
} from "react-md";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { auditoriaPrimeiroTurno } from "./seletores";

import google from "../seguranca/google.svg";
import facebook from "../seguranca/facebook.svg";

const Auditoria = ({ auditoriaPrimeiroTurno }) =>
  <Grid noSpacing={false}>
    {
      auditoriaPrimeiroTurno.map((usuario, i) => (
        <Card key={i} className="md-cell md-cell--12">
          <CardTitle
            title={usuario.nome}
            subtitle={
              usuario.conta === "google.com" ? <Avatar src={google} role="presentation" /> : <Avatar src={facebook} role="presentation" />
            }
            avatar={<Avatar src={usuario.foto} role="presentation" />}
          />
          <CardText>
            <DataTable plain>
              <TableHeader>
                <TableRow>
                  <TableColumn adjusted={false} style={{ "padding": "0" }}>Rodada</TableColumn>
                  <TableColumn adjusted={false} style={{ "padding": "0" }}>Jogos</TableColumn>
                  <TableColumn adjusted={false} style={{ "padding": "0" }}></TableColumn>
                  <TableColumn adjusted={false} style={{ "padding": "0" }}></TableColumn>
                </TableRow>
              </TableHeader>
              <TableBody>
                {
                  usuario.rodadas.map((jogo, i) =>
                    <TableRow key={i}>
                      <TableColumn adjusted={false} style={{ "padding": "0", "height": "30px" }}>{jogo.rodada}</TableColumn>
                      <TableColumn colSpan={3} adjusted={false} style={{ "padding": "0", "height": "30px" }}>{`${jogo.mandante_nome} ${jogo.mandante_placar} vs ${jogo.visitante_placar} ${jogo.visitante_nome}`}</TableColumn>
                    </TableRow>
                  )
                }
              </TableBody>
            </DataTable>
          </CardText>
        </Card>
      ))
    }
  </Grid>

const mapStateToProps = state => {
  return { auditoriaPrimeiroTurno: auditoriaPrimeiroTurno(state) };
};

const mapDispatchToProps = dispatch => bindActionCreators({}, dispatch)

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Auditoria))
