import React, { Component } from "react";
import { Portal } from 'react-md';
import "./index.css";
import logo from "./logo.svg";

class Carregando extends Component {
  constructor(props) {
    super(props)
    this.state = {
      contador: 0
    }
  }
  cancelar = () => {
    if (this.state.contador === 5) {
      this.setState({ contador: 0 });
      this.props.cancelar();
    }
    else {
      this.setState({ contador: this.state.contador + 1 });
    }
  }
  render() {
    return <Portal visible={this.props.carregando}>
      <div>
        <img onClick={this.cancelar} src={logo} className="carregando-logo" alt="logo" />
        <div className="carregando" />
      </div>
    </Portal>
  }
}
export default Carregando
