import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { buscarPalpites as buscar, salvarPalpites as salvar, quandoMudar } from "./actions";
import { atualizar, base10, primeiroTurno } from "../admin/actions";
import Rodada from "../rodada";
import { buscarJogos, classificacao } from "../classificacao/seletores";

class Palpite extends Component {

  componentDidMount() {
    const { rodadaSelecionada, jogos, buscar, usuario: { uid: usuarioId } } = this.props;
    if (jogos.length === 0) {
      buscar(rodadaSelecionada, usuarioId);
    }
  }

  render() {
    return <Rodada {...this.props} />
  }
}

const mapStateToProps = (state, ownProps) => {
  const usuarioId = ownProps.match.params.usuarioId;
  const rodadaSelecionada = state.palpite.rodadaSelecionada;
  const classificacaoUsuario = classificacao(state).find(usuario => usuario.uid === usuarioId).classificacao;
  const {
    usuario,
    jogos,
    pontos,
    placares
  } = buscarJogos(state, usuarioId, rodadaSelecionada);

  let {
    config: {
      campeonato: { rodadas } = {},
      bloquear
    } = {}
  } = state.admin;
  const ehPrimeiroTurno = false;//parseInt(rodadaSelecionada, base10) <= primeiroTurno
  const exibirPlacar = bloquear || state.seguranca.usuarioLogado.uid === usuarioId;
  const exibirBotoes = state.seguranca.usuarioLogado.uid === usuarioId && !bloquear && parseInt(rodadaSelecionada) > 19;

  return { jogos, rodadaSelecionada, rodadas, usuario, pontos, placares, classificacaoUsuario, exibirPlacar, exibirBotoes };
}

const mapDispatchToProps = dispatch => bindActionCreators({
  buscar,
  salvar,
  quandoMudar,
  atualizar
}, dispatch)

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Palpite))
