import firebase from "firebase";
import { adicionarMensagens } from "../app/actions";
import { CARREGANDO_DESLIGADO, ligarCarregando } from "../carregando/actions";
export const USUARIOS_LIMPAR = "usuario/USUARIO_LIMPAR";
export const USUARIOS_LISTAR = "usuario/USUARIOS_LISTAR"
export const ADICIONAR_USUARIO = "usuario/ADICIONAR_USUARIO";
export const ADICIONAR_USUARIOS = "usuario/ADICIONAR_USUARIOS";
export const REMOVER_USUARIO = "usuario/REMOVER_USUARIO";

export const buscarTodos = (forcarAtualizacao = false) => (dispatch, getState) => {
  dispatch(ligarCarregando());
  if (forcarAtualizacao) {
    return firebase
      .firestore()
      .collection("usuarios")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const usuario = { [doc.id]: { ...doc.data() } };
          dispatch({ type: ADICIONAR_USUARIO, usuario })
        });
        dispatch({ type: CARREGANDO_DESLIGADO });
        return Promise.resolve(`Todos usurios em cache`);
      })
      .catch(erros => Promise.reject(dispatch(adicionarMensagens(erros))));
  }
  else {
    return dispatch({ type: CARREGANDO_DESLIGADO });
  }
}

export const buscar = (usuarioId, forcarAtualizacao = false) => (dispatch, getState) => {
  dispatch(ligarCarregando());
  if (usuarioId) {
    const usuario = getState().usuario.usuarios[usuarioId];
    if (usuario) {
      dispatch({ type: CARREGANDO_DESLIGADO });
      return Promise.resolve(`Usurio ${usuario.uid} em cache`);
    }
    return firebase
      .firestore()
      .collection("usuarios")
      .doc(usuarioId)
      .get()
      .then(doc => {
        const usuario = { [doc.id]: { ...doc.data() } };
        dispatch({ type: ADICIONAR_USUARIO, usuario });
        dispatch({ type: CARREGANDO_DESLIGADO });
        return Promise.resolve(`Usurio ${usuario.uid} buscado remotamente e colocado em cache`);
      })
      .catch(erros => Promise.reject(dispatch(adicionarMensagens(erros))));
  }
}

export const mapearUsuarioBolao = (usuarioFirebase = {}) => {

  const { providerId = "", uid = "", photoURL = "", displayName = "" } = usuarioFirebase.providerData[0] || {};
  return {
    nome: displayName || usuarioFirebase.displayName,
    email: usuarioFirebase.email,
    foto: "facebook.com" === providerId ? `https://graph.facebook.com/${uid}/picture` : photoURL,
    uid: usuarioFirebase.uid,
    providerId,
    ultimoLogin: usuarioFirebase.metadata.lastSignInTime
  }
}

export const limparUsuarios = () => dispatch => {

  dispatch({
    type: USUARIOS_LIMPAR
  });
}




