import { createStore, applyMiddleware, compose } from "redux";
import thunk from "redux-thunk";
import rootReducer from "./app/reducers";
import { composeWithDevTools } from "redux-devtools-extension";
import storage from "redux-persist/lib/storage";
import { persistStore, persistReducer } from "redux-persist";
import { atualizarSituacaoUsuarioLogado, depoisDoLoginRedirect } from "./seguranca/actions";

import firebase from "firebase";
import "firebase/firestore";

const config = {
  apiKey: "AIzaSyBb46_Qu1bo3QR8x47LnKdYt9BheD2P0pc",
    authDomain: "combinados.firebaseapp.com",
    databaseURL: "https://combinados.firebaseio.com",
    projectId: "firebase-combinados",
    storageBucket: "firebase-combinados.appspot.com",
    messagingSenderId: "607735444379"
};
firebase.initializeApp(config);

const persistConfig = {
  key: "root",
  storage: storage,
}

const persistedReducer = persistReducer(persistConfig, rootReducer);

const initialState = {}
const enhancers = []
const middleware = [
  thunk
]
let meuCompose = compose;
if (process.env.NODE_ENV === "development") {
  meuCompose = composeWithDevTools;
}

const composedEnhancers = meuCompose(
  applyMiddleware(...middleware),
  ...enhancers
)

const store = createStore(
  persistedReducer,
  initialState,
  composedEnhancers
);

export const persistor = persistStore(store)

export default store;
store.dispatch(atualizarSituacaoUsuarioLogado());
store.dispatch(depoisDoLoginRedirect());

