import React from "react";
// import { push } from "react-router-redux"
import { Link } from "react-router-dom";

import {
  Avatar,
  Divider,
  List,
  ListItem,
  Subheader,
  Button
} from "react-md";

import { MdSync } from "react-icons/lib/md";

const ListaUsuarios = props => {


  const usuariosPremiados = props.usuarios.filter(usuario => usuario.classificacao < 3);
  const uidsPremiados = usuariosPremiados.map(premiado => premiado.uid);
  const usuariosNaoPremiados = props.usuarios.filter(usuario => !uidsPremiados.includes(usuario.uid));

  const pegarFoto = ({ providerId, foto }) => {
    if (foto.includes("fbcdn")) {
      return `https://m.facebook.com/photo.php?fbid=${foto.split("_")[1]}`
      // return fetch(`https://m.facebook.com/photo.php?fbid=${foto.split("_")[1]}`)
      //   .then(r => {
      //     return r.text()
      //   })
      //   .then(html => {
      //     return "";
      //   })
      //   .catch(e => {
      //     return ""
      //   })
    }

    if (foto.includes("asid=")) {
      return foto.replace(/.*asid=([\d]+).*/, "https://graph.facebook.com/$1/picture");
    }

    return foto;
  }

  return <div className="md-grid">
    <List className="md-cell md-cell--8-desktop md-cell--2-desktop-offset md-paper md-paper--1">
      <Subheader primaryText="Premiados">
        {
          !props.ehSimulacao &&
          <Button onClick={props.atualizarTudo} primary icon secondary><MdSync size={20} /></Button>
        }
      </Subheader>
      <Divider inset />
      {
        usuariosPremiados.map(usuario => (
          < ListItem key={usuario.uid}
            component={Link}
            to={`palpite/${usuario.uid}`}
            leftAvatar={<Avatar src={pegarFoto(usuario)} role="presentation" />}
            rightAvatar={<Avatar suffix="green">{usuario.classificacao}</Avatar>}
            primaryText={usuario.nome}
            secondaryText={`Pontos: ${usuario.pontos}, Placares: ${usuario.placares}`}
          />
        ))
      }
      <Divider />
      {
        usuariosNaoPremiados.map(usuario => (
          < ListItem key={usuario.uid}
            component={Link}
            to={`palpite/${usuario.uid}`}
            leftAvatar={<Avatar src={usuario.foto} role="presentation" />}
            rightIcon={<Avatar suffix="grey">{usuario.classificacao}</Avatar>}
            primaryText={usuario.nome}
            secondaryText={`Pontos: ${usuario.pontos}, Placares: ${usuario.placares}`}
          />
        ))
      }
    </List>
  </div>
}
export default ListaUsuarios;