import { ehAdmin } from "seguranca/actions";

export const buscarJogos = state => {
  let { rodadaSelecionada, jogos } = state.admin;
  const admin = ehAdmin(state);
  if (admin) {
    jogos = state.admin.jogosGabarito;
  }
  jogos = Object
    .keys(jogos)
    .filter(jogoId => jogos[jogoId].rodada === rodadaSelecionada)
    .map(jogoId => ({ jogoId, ...jogos[jogoId] }))
    .sort((a, b) => ('' + a.mandante_nome).localeCompare(b.mandante_nome));
  return {
    jogos,
    rodadaSelecionada,
    exibirBotoes: admin,
    exibirPlacar: admin
  }
}


export const situacaoPreenchimento = ({ usuario: { usuarios }, palpite: { palpites } }) =>
  Object.keys(usuarios).map(i => ({
    nome: usuarios[i].nome,
    conta: usuarios[i].providerId,
    foto: usuarios[i].foto,
    uid: usuarios[i].uid,
    rodadas: palpites
      .filter(p => p.usuarioId === usuarios[i].uid && p.mandante_placar !== null && p.visitante_placar !== null)
      .map(p => parseInt(p.rodada, 10))
      .reduce((acc, curr) => {
        if (typeof acc[curr] === 'undefined') {
          acc[curr] = 1;
        }
        else {
          acc[curr] += 1;
        }
        return acc;
      }, {})
  }));

export const auditoriaPrimeiroTurno = ({ usuario: { usuarios }, palpite: { palpites }, admin: { jogos } }) =>
  Object.keys(usuarios).map(i => ({
    nome: usuarios[i].nome,
    conta: usuarios[i].providerId,
    foto: usuarios[i].foto,
    rodadas: palpites
      .filter(p => p.usuarioId === usuarios[i].uid && parseInt(p.rodada, 10) <= 19)
      .map(p => ({ ...jogos[p.jogoId], ...p }))
      .sort((a, b) => parseInt(a.rodada, 10) - parseInt(b.rodada, 10))
  }));