import React from "react";
import { Divider, Button, Cell, Grid, AccessibleFakeInkedButton, ListItem, Avatar } from "react-md";
import Jogos from "./jogos"
const Rodada = props => {
  const { usuario = {}, jogos, rodadaSelecionada } = props;
  return <Grid noSpacing={true}>

    <Cell phoneSize={4} desktopSize={12}>
      {
        props.usuario
          ?
          <ListItem style={{ listStyle: "none" }} key={usuario.uid}
            leftAvatar={<Avatar src={usuario.foto} role="presentation" />}
            rightIcon={<Avatar suffix={props.classificacaoUsuario <= 2 ? "green" : "gray"}>{`${props.classificacaoUsuario}`}</Avatar>}
            primaryText={`Palpites: ${rodadaSelecionada} rodada`}
            secondaryText={`Pontos: ${props.pontos}, Placares: ${props.placares}`} />
          :
          <ListItem style={{ listStyle: "none" }}
            primaryText={`${props.ehSimulacao ? "Simulador: " : "Gabarito: "}  ${rodadaSelecionada} rodada`}
            secondaryText={`${props.ehSimulacao ? "" : "Admin: Reinaldo Vale"}`} />
      }
      <Divider />
    </Cell>

    <Cell phoneSize={1} desktopSize={4}>
      <Grid noSpacing={true}>
        {
          [...new Array(props.rodadas)].map((_, i) =>
            <Cell key={i} phoneSize={2} desktopSize={2}>
              <AccessibleFakeInkedButton
                className={`${parseInt(rodadaSelecionada, 10) === i + 1 ? "md-background--secondary" : ""} md-btn md-btn--icon md-btn--floating md-btn--raised-pressed md-btn--floating-mini md-pointer--hover md-paper md-paper--2`}
                onClick={e => { props.buscar(i + 1 + "", usuario.uid) }}
                style={{ width: "80%", height: "85%", padding: "21%" }}>
                {i + 1}
              </AccessibleFakeInkedButton>
            </Cell>
          )
        }
      </Grid>
    </Cell>
    <Cell phoneSize={3} desktopSize={8}>
      <Jogos {...props} />
    </Cell>
    {
      props.exibirBotoes &&
      <Cell phoneSize={4} desktopSize={12} className="md-text-center" >
        <Button raised primary onClick={e => props.salvar(jogos, usuario.uid)}>Salvar</Button> {"  "}
        {/* <Button raised primary onClick={e => props.atualizar(rodadaSelecionada, usuario.uid)}>Atualizar</Button> */}
      </Cell>
    }
  </Grid>;
}
export default Rodada;