import React, { Component } from "react";
import {
  Avatar,
  FontIcon,
  AccessibleFakeButton,
  DropdownMenu
} from "react-md";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import {
  entrarComGoogle,
  entrarComFacebook,
  sair
} from "./actions";
import google from "./google.svg";
import deslogado from "./deslogado.svg";
import facebook from "./facebook.svg";
import { MdExitToApp } from "react-icons/lib/md";

class Login extends Component {
  render() {
    const { nome: nomeUsuarioLogado = "Deslogado", foto = deslogado } = this.props.usuarioLogado || {};
    const itens = [
      {
        primaryText: nomeUsuarioLogado,
        rightAvatar: <Avatar src={foto} role="presentation" />
      },
      {
        divider: true
      },
      {
        key: "entrarGoogle",
        onClick: this.props.entrarComGoogle,
        leftAvatar: <Avatar src={google} role="presentation" />,
        primaryText: "Google",
        secondaryText: "Entrar com o google"

      },
      {
        key: "entrarFacebook",
        onClick: this.props.entrarComFacebook,
        leftAvatar: <Avatar src={facebook} role="presentation" />,
        primaryText: "Facebook",
        secondaryText: "Entrar com o facebook"
      },
      { divider: true },
      {
        key: "sair_do_bolao",
        onClick: this.props.sair,
        primaryText: "Sair",
        leftIcon: <FontIcon><MdExitToApp /></FontIcon>
      }];
    return (
      <div>
        <DropdownMenu
          id="avatar-dropdown-menu"
          listHeightRestricted={false}
          menuItems={itens}
        >
          <AccessibleFakeButton>
            <Avatar src={foto} role="presentation" className="md-btn md-btn--raised-pressed" style={{ width: "54px", height: "54px" }} />
          </AccessibleFakeButton>
        </DropdownMenu>
        {/* <p>{nomeUsuarioLogado.replace(/^(.{8}).*$/, "$1...")}</p> */}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  usuarioLogado: state.seguranca.usuarioLogado,
  erros: state.seguranca.erros,
})

const mapDispatchToProps = dispatch => bindActionCreators({
  entrarComGoogle,
  entrarComFacebook,
  sair
}, dispatch)

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Login))
