import {
  ADICIONAR_PALPITE,
  ADICIONAR_PALPITES,
  RODADA_SELECIONADA,
  REMOVER_PALPITES
} from "./actions";

const initialState = {
  palpites: [],
  rodadaSelecionada: "1"
}

export default (state = initialState, action) => {
  switch (action.type) {
    case RODADA_SELECIONADA:
      return {
        ...state,
        rodadaSelecionada: action.rodadaId
      }
    case REMOVER_PALPITES: {
      return { ...state, palpites: state.palpites.filter(p => !action.palpites.includes(p.id)) };
    }
    case ADICIONAR_PALPITES: {
      return { ...state, palpites: action.palpites };
    }
    case ADICIONAR_PALPITE: {
      const palpite = { ...state.palpites.find(p => p.id === action.palpite.id), ...action.palpite };
      return { ...state, palpites: [...state.palpites.filter(p => p.id !== palpite.id), { ...palpite }] };
    }
    default:
      return state
  }
}