import React, { Component, Fragment } from "react";
import { Button, LinearProgress } from "react-md";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import Rodada from "../rodada"
import {
  buscarGabarito as buscar,
  salvarRodada as salvar,
  quandoMudar,
  atualizar,
  atualizarTudo,
  enviarTudo,
  apagarCache
} from "./actions";
import { buscarJogos } from "./seletores";

import { MdSync, MdDelete, MdPublish, MdLockOutline, MdLockOpen } from "react-icons/lib/md";

class Admin extends Component {
  constructor(props) {
    super(props)
    this.state = {
      menuSecreto: false,
      contador: 0
    }
  }

  componentDidMount() {
    const { rodadaSelecionada, jogos, buscar } = this.props;
    if (jogos.length === 0) {
      buscar(rodadaSelecionada);
    }
  }

  exibirMenuSecreto = () => {
    if (this.state.contador === 5) {
      this.setState({ contador: 0 });
      this.setState({ menuSecreto: true });
    }
    else {
      this.setState({ contador: this.state.contador + 1 });
      this.setState({ menuSecreto: false });
    }
  }

  render() {
    return <div>
      <Rodada {...this.props} />
      {
        this.state.menuSecreto && this.props.exibirBotoes &&
        <Fragment>
          <Button onClick={apagarCache} title="Apagar Tudo" icon primary secondary><MdDelete /></Button>
          <Button onClick={this.props.atualizarTudo} icon primary secondary><MdSync /></Button>
          <Button onClick={this.props.enviarTudo} icon primary secondary><MdPublish /></Button>
          <Button onClick={this.props.bloquear} icon primary secondary><MdLockOutline /></Button>
          <Button onClick={this.props.desbloquear} icon primary secondary><MdLockOpen /></Button>
        </Fragment>
      }
      <LinearProgress onClick={this.exibirMenuSecreto} id="query-indeterminate-progress" query />
    </div>
  }
}

const mapStateToProps = state => {
  const ehGabarito = true;
  let {
    config: {
      campeonato: { rodadas } = {}
    } = {}
  } = state.admin;
  return { ...buscarJogos(state), ehGabarito, rodadas };
};

const mapDispatchToProps = dispatch => bindActionCreators({
  buscar,
  salvar,
  quandoMudar,
  atualizar,
  atualizarTudo,
  enviarTudo,
}, dispatch)

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Admin))
