import {
  RODADA_SELECIONADA,
  ADICIONAR_PLACAR,
  ADICIONAR_JOGO_SIMULADOR
} from "./actions";

const initialState = {
  rodadaSelecionada: "1",
  jogosSimulador: {}
}

export default (state = initialState, action) => {
  switch (action.type) {
    case ADICIONAR_JOGO_SIMULADOR:
      return { ...state, jogosSimulador: { ...state.jogosSimulador, ...action.jogoSimulador } };
    case ADICIONAR_PLACAR: {
      const { id: jogoId, ...time } = action.time;
      return { ...state, jogosSimulador: { ...state.jogosSimulador, [jogoId]: { ...state.jogosSimulador[jogoId], ...time } } };
    }
    case RODADA_SELECIONADA:
      return { ...state, rodadaSelecionada: action.rodadaId }
    default:
      return state
  }
}