import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { Route, Switch, withRouter } from "react-router-dom"
import Classificacao from "../classificacao";
import { NavigationDrawer, Snackbar, FontIcon } from "react-md";
import Login from "../seguranca";
import NavLink from "./NavLink";
import logo from "../carregando/logo.svg";

import Simulador from "../simulador";
import Admin from "../admin";
import Situacao from "../admin/Situacao";
import Auditoria from "../admin/Auditoria";
import Palpite from "../palpite";
import Regra from "../regra";

import Carregando from "../carregando";
import { cancelar } from "../carregando/actions";
import { limparMensagens } from "./actions";

import { MdSort, MdSchool, MdFunctions, MdNewReleases, MdCompareArrows, MdGavel, MdMenu, MdArrowBack } from "react-icons/lib/md";
import { buscarConfig } from "admin/actions";

const navItems = [{
  exact: true,
  label: "Classificacao",
  to: "/",
  icon: <MdSort />,
  exibir: true
},
// {
//   label: "Usurio",
//   to: "/usuario",
//   icon: "bookmark",
// }, 
{
  label: "Gabarito",
  to: "/admin",
  icon: <MdSchool />,
  exibir: true
},
{
  label: "Simulador",
  to: "/simulador",
  icon: <MdFunctions />,
  exibir: true
},
{
  label: "Regras",
  to: "/regras",
  icon: <MdNewReleases />,
  exibir: true
},
{
  label: "Preenchimento",
  to: "/situacao",
  icon: <MdCompareArrows />,
  exibir: true
},
{
  label: "Auditoria",
  to: "/auditoria",
  icon: <MdGavel />
}
];

class App extends Component {
  componentDidMount() {
    this.props.buscarConfig();
  }
  render() {
    const itens = navItems.filter(({ label }) => {
      if (label === "Auditoria") {
        return this.props.bloquear
      }
      return true;
    });

    return (
      <Route
        render={({ location }) => (
          <NavigationDrawer
            temporaryIcon={<FontIcon><MdMenu /></FontIcon>}
            persistentIcon={<FontIcon><MdArrowBack /></FontIcon>}
            drawerTitle={<img alt="" src={logo} />}
            toolbarTitle={this.props.titulo}
            navItems={itens.map(props => <NavLink {...props} key={props.to} />)}
            toolbarActions={<Login />}
          >
            <Switch>
              <Route exact path="/" component={Classificacao} />
              <Route path="/simulador" component={Simulador} />
              {/* <Route path="/usuario" component={Usuario} /> */}
              <Route path="/palpite/:usuarioId" component={Palpite} />
              <Route path="/admin" component={Admin} />
              <Route path="/regras" component={Regra} />
              <Route path="/situacao" component={Situacao} />
              <Route path="/auditoria" component={Auditoria} />
            </Switch>
            <Snackbar
              id="mensagens"
              toasts={this.props.mensagens}
              autohide={true}
              autohideTimeout={10000}
              onDismiss={this.props.limparMensagens} />
            <Carregando {...this.props} />
          </NavigationDrawer>

        )}
      />
    );
  }
}

const mapStateToProps = state => {
  let {
    config: {
      campeonato: { nome: titulo, iniciado },
      bloquear
    } = {}
  } = state.admin;
  return {
    mensagens: state.app.mensagens,
    carregando: state.carregando.carregando,
    titulo,
    iniciado,
    bloquear
  }
};

const mapDispatchToProps = dispatch => bindActionCreators({
  limparMensagens,
  buscarConfig,
  cancelar
}, dispatch)

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(App));

// export default App;