import firebase from "firebase";
import { adicionarMensagens } from "../app/actions";
import { CARREGANDO_DESLIGADO, ligarCarregando } from "../carregando/actions";
import { buscar as buscarUsuarios } from "../usuario/actions";
import { buscarRodada, MAXIMO_JOGOS_RODADA } from "../admin/actions";
import { base10, primeiroTurno } from "../admin/actions";

export const RODADA_SELECIONADA = "palpite/RODADA_SELECIONADA";
export const ADICIONAR_PALPITE = "palpite/ADICIONAR_PALPITE";
export const ADICIONAR_PALPITES = "palpite/ADICIONAR_PALPITES";
export const MESCLAR_PALPITE_GABARITO = "palpite/MESCLAR_PALPITE_GABARITO";
export const REMOVER_PALPITES = "palpite/REMOVER_PALPITES";


export const buscarPalpites = (rodadaId = "1", usuarioId) => (dispatch, getState) => {
  dispatch(ligarCarregando());
  dispatch({ type: RODADA_SELECIONADA, rodadaId });
  dispatch(verificarDependencias(rodadaId, usuarioId))
    .then(retorno => {
      // var fire = firebase.firestore();
      const palpites = getState().palpite.palpites;
      const rodadaPalpites = palpites.filter(palpite => palpite.usuarioId === usuarioId && palpite.rodada === rodadaId);

      if (rodadaPalpites.length === MAXIMO_JOGOS_RODADA) {
        return Promise.resolve({ type: CARREGANDO_DESLIGADO });
      }

      dispatch(ligarCarregando());
      return firebase.firestore().collection("palpites")
        .where("usuarioId", "==", usuarioId)
        .where("rodada", "==", rodadaId)
        .get()
        .then(querySnapshot => {
          if (querySnapshot.size === MAXIMO_JOGOS_RODADA) {
            querySnapshot.forEach(doc => {
              const palpite = { id: doc.id, ...doc.data() };
              dispatch({ type: ADICIONAR_PALPITE, palpite })
            });
            return Promise.resolve({ type: CARREGANDO_DESLIGADO });
          }

          if (querySnapshot.metadata.fromCache) {
            dispatch(adicionarMensagens("Voc est off-line ou a cota diria de consultas no servidor acabou, aguarde at amanh."))
            return Promise.resolve({ type: CARREGANDO_DESLIGADO });
          }

          let palpites = [];
          querySnapshot.forEach(doc => {
            palpites = [{ ...rodadaPalpites.find(p => p.id === doc.id), id: doc.id, ...doc.data() }];
          });

          palpites = [...palpites, ...rodadaPalpites.filter(p => !palpites.map(({ id }) => id).includes(p.id))]

          return dispatch(reconciliarBase(palpites, usuarioId, rodadaId));
        })
        .catch(erros => {
          dispatch(adicionarMensagens(erros));
          return Promise.resolve({ type: CARREGANDO_DESLIGADO });
        });
    })
    .then(action => {
      dispatch(action);
    })
    .catch(erros => {
      dispatch(adicionarMensagens(erros));
    });
}

const reconciliarBase = (palpites, usuarioId, rodadaId) => (dispatch, getState) => {
  if (palpites.length < MAXIMO_JOGOS_RODADA) {
    // Salvar Palpites default (copia do gabarito)
    const jogos = getState().admin.jogos;
    const rodadaGabarito = Object
      .keys(jogos)
      .filter(jogoId => jogos[jogoId].rodada === rodadaId)
      .map(jogoId => ({ jogoId, ...jogos[jogoId] }));
    const palpitesVazios = rodadaGabarito.map(jogo => ({ ...jogo, mandante_placar: null, visitante_placar: null }));
    palpites = [...palpites, ...palpitesVazios.filter(p => !palpites.map(({ jogoId }) => jogoId).includes(p.jogoId))]
    return dispatch(salvarPalpites(palpites, usuarioId));
  }

  if (palpites.length > MAXIMO_JOGOS_RODADA) {
    const batch = firebase.firestore().batch();

    palpites = getState().palpite.palpites.filter(p => p.usuarioId === usuarioId);
    const p = palpites
      .filter(p => palpites
        .filter(pg => p.rodada === pg.rodada && p.usuarioId === pg.usuarioId && p.jogoId === pg.jogoId).length > 1
      )
      .filter(p => p.mandante_placar === null)
      .map(({ id }) => {
        const palpiteDocRef = firebase.firestore().collection("palpites").doc(id);
        batch.delete(palpiteDocRef);
        return id;
      });

    return batch.commit()
      .then(() => {
        dispatch(adicionarMensagens("Palpites Atualizados com Sucesso!"));
        dispatch({ type: REMOVER_PALPITES, palpites: p });
        return Promise.resolve({ type: CARREGANDO_DESLIGADO });
      })
      .catch(erros => {
        dispatch(adicionarMensagens(erros));
        return Promise.reject(erros)
      });
  }
}

export const salvarPalpites = (palpites, usuarioId) => (dispatch, getState) => {
  if (verificarPalpitesRepetidos(palpites)) {
    return Promise.resolve(dispatch(adicionarMensagens("Palpites repetidos mais de 3 vezes")));
  } 

  dispatch(ligarCarregando());
  const batch = firebase.firestore().batch();
  palpites.forEach(p => {
    const palpiteDocRef = p.id
      ? firebase.firestore().collection("palpites").doc(p.id)
      : firebase.firestore().collection("palpites").doc();
    const { jogoId, mandante_placar, visitante_placar, rodada } = p;
    const palpiteCorpo = { jogoId, usuarioId, mandante_placar, visitante_placar, rodada };
    batch.set(palpiteDocRef, { ...palpiteCorpo });
    // TODO: transportar isso para dentro do commit
    const palpite = { id: palpiteDocRef.id, ...palpiteCorpo };
    dispatch({ type: ADICIONAR_PALPITE, palpite });
  });
  return batch.commit()
    .then(() => {
      dispatch(adicionarMensagens("Palpites Atualizados com Sucesso!"));
      return Promise.resolve({ type: CARREGANDO_DESLIGADO });
    })
    .catch(erros => {
      dispatch(adicionarMensagens(erros));
      return Promise.reject(erros)
    });
}

export const quandoMudar = palpite => (dispatch, getState) => {
  const { config: { bloquear } } = getState().admin;
  const { palpites: todosPalpites, rodadaSelecionada } = getState().palpite;
  if (bloquear) {
    return dispatch(adicionarMensagens("Bolo bloqueado!"));
  }
  const { usuarioId, rodada } = todosPalpites.find(p => p.id === palpite.id);

  if (parseInt(rodada, base10) <= primeiroTurno && bloquear) {
    // return dispatch(adicionarMensagens("Primeiro turno bloqueado!"));
  }

  let palpites = todosPalpites.filter(p => p.rodada === rodadaSelecionada && p.usuarioId === usuarioId);

  palpite = { ...palpites.find(p => p.id === palpite.id), ...palpite };
  palpites = [...palpites.filter(p => p.id !== palpite.id), { ...palpite }];

  if (verificarPalpitesRepetidos(palpites)) {
    return dispatch(adicionarMensagens("Palpites repetidos mais de 3 vezes"));
  }
  dispatch({ type: ADICIONAR_PALPITE, palpite });
}

const verificarPalpitesRepetidos = palpites => {
  let repetidos = {};
  palpites
    .filter(p => p.mandante_placar !== null && p.visitante_placar !== null)
    .map(p => `pm${p.mandante_placar}_pv${p.visitante_placar}`)
    .forEach(p => repetidos[p] = repetidos[p] ? repetidos[p] + 1 : 1);
  return (Object.keys(repetidos).find(chave => repetidos[chave] > 3));
}

export const verificarDependencias = (rodadaId, usuarioId) => (dispatch, getState) => Promise
  .all([dispatch(buscarUsuarios(usuarioId)), dispatch(buscarRodada(rodadaId))]);