import React, { Component } from "react";
import { Button, Cell } from "react-md";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import Rodada from "../rodada"
import { quandoMudar } from "./actions";
import { buscarRodada as buscar } from "./actions";
import { buscarJogos } from "./seletores";
import { classificacao } from "../classificacao/seletores";
import ListaUsuarios from "../usuario/ListaUsuarios";


class Simulador extends Component {
  state = { classificacao: false };
  componentDidMount() {
    const { rodadaSelecionada, jogos, buscar } = this.props;
    if (jogos.length === 0) {
      buscar(rodadaSelecionada);
    }
  }

  exibirClassificacao = () => this.setState({ classificacao: !this.state.classificacao });

  render() {
    return this.state.classificacao
      ?
      <div>
        <ListaUsuarios {...this.props} />
        <Cell phoneSize={4} desktopSize={12} className="md-text-center" >
          <Button raised primary onClick={this.exibirClassificacao}>Voltar</Button>
        </Cell>
      </div>
      :
      <Rodada {...{ ...this.props, salvar: this.exibirClassificacao }} />
  }
}

const mapStateToProps = state => {
  const { ehSimulacao = true, exibirPlacar = true, exibirBotoes = true } = {};
  const usuarios = classificacao(state, state.simulador.rodadaSelecionada);
  let {
    config: {
      campeonato: { rodadas } = {}
    } = {}
  } = state.admin;
  return { ...buscarJogos(state), usuarios, ehSimulacao, exibirPlacar, exibirBotoes, rodadas };
  // return { jogos:state.simulador.jogosSimulador, ehSimulacao, exibirPlacar, exibirBotoes };

};

const mapDispatchToProps = dispatch => bindActionCreators({
  quandoMudar,
  buscar
}, dispatch)

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Simulador))
