import React, { Component } from "react";
// import { push } from "react-router-redux"
import { withRouter } from "react-router-dom";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { atualizarTudo, verificarConexao } from "../admin/actions";
import { classificacao } from "./seletores";
import ListaUsuarios from "../usuario/ListaUsuarios";

class Classificacao extends Component {

  componentDidMount() {
    this.props.verificarConexao();
  }

  render() {
    return <ListaUsuarios {...this.props} />
  }
}

const mapStateToProps = state => ({
  usuarios: classificacao(state)
})

const mapDispatchToProps = dispatch => bindActionCreators({
  atualizarTudo,
  verificarConexao
  // changePage: () => push("/about-us")
}, dispatch)

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Classificacao))
