import {CARREGANDO_DESLIGADO, CARREGANDO_LIGADO} from "./actions"

const initialState = {
  carregando: false
}

export default (state = initialState, action) => {
  switch (action.type) {
    case CARREGANDO_LIGADO:
      return {
        ...state,
        carregando: true
      }
    case CARREGANDO_DESLIGADO:
      return {
        ...state,
        carregando: false
      }

    default:
      return state
  }
}