import firebase from "firebase";
import { adicionarMensagens } from "../app/actions";
import { CARREGANDO_DESLIGADO, ligarCarregando } from "../carregando/actions";
import { mapearUsuarioBolao, ADICIONAR_USUARIO } from "../usuario/actions";
import { atualizarTudo } from "../admin/actions";

export const ENTRAR = "seguranca/ENTRAR";
export const SAIR = "seguranca/SAIR";

export const ehAdmin = state => {
  let {
    seguranca: { usuarioLogado },
    admin: {
      config: { usuarioAdmin = {} } = {}
    }
  } = state;
  return usuarioAdmin.uid && usuarioLogado.uid === usuarioAdmin.uid;
};

export const sair = () => dispatch => {
  firebase.auth().signOut()
    .then(function () {
      dispatch({
        type: SAIR
      });
    })
    .catch(erros => {
      dispatch(adicionarMensagens(erros));
    });
}

export const entrarComGoogle = () => dispatch => {
  dispatch(ligarCarregando());

  const provider = new firebase.auth.GoogleAuthProvider();
  firebase.auth().languageCode = "pt_BR";
  firebase.auth().signInWithRedirect(provider)
    .catch(erros => {
      dispatch(adicionarMensagens(erros));
      // dispatch(sair());
      dispatch({
        type: CARREGANDO_DESLIGADO
      });
    });
}

