export const atualizarSituacaoUsuarioLogado = () => dispatch => {

  firebase.auth().onAuthStateChanged(usuarioFirebase => {

    if (usuarioFirebase) {
      dispatch(atualizarUsuario(mapearUsuarioBolao(usuarioFirebase)));
    }
    else {
      dispatch(sair());
    }
  });
}

export const atualizarUsuario = (usuarioLogado = {}) => (dispatch, getState) => {

  const { seguranca: { usuarioLogado: usuarioLogadoCache } } = getState();

  if (usuarioLogadoCache
    && usuarioLogado.providerId === usuarioLogadoCache.providerId
    && usuarioLogado.foto === usuarioLogadoCache.foto
    && usuarioLogado.nome === usuarioLogadoCache.nome) {
    return dispatch({ type: CARREGANDO_DESLIGADO });
  }

  const usuarioDocRef = firebase.firestore().collection("usuarios").doc(usuarioLogado.uid);
  firebase.firestore().runTransaction(transaction => {
    return transaction.get(usuarioDocRef)
      .then(usuarioDoc => {
        if (usuarioDoc.exists) {
          transaction.update(usuarioDocRef, { ...usuarioLogado });
        }
        else {
          transaction.set(usuarioDocRef, { ...usuarioLogado });
        }
        return usuarioDoc.exists;
      })
  })
    .then(usuarioExiste => {
      console.log("Transaction successfully committed!");

      if (!usuarioExiste) {
        return dispatch(atualizarTudo())
          .then(resposta => {
            dispatch({ type: ENTRAR, usuarioLogado });
            const usuario = { [usuarioLogado.uid]: { ...usuarioLogado } };
            dispatch({ type: ADICIONAR_USUARIO, usuario })
            dispatch({ type: CARREGANDO_DESLIGADO });
          })
          .catch(erros => {
            // dispatch({ type: ENTRAR, usuarioLogado });
            dispatch(adicionarMensagens(erros))
          });
      }

      dispatch({ type: ENTRAR, usuarioLogado });
      const usuario = { [usuarioLogado.uid]: { ...usuarioLogado } };
      dispatch({ type: ADICIONAR_USUARIO, usuario })
      dispatch({ type: CARREGANDO_DESLIGADO });
    })
    .catch(erros => {
      dispatch(adicionarMensagens(erros))
    });
}