import {
  RODADA_SELECIONADA,
  ADICIONAR_JOGO,
  ADICIONAR_JOGOS,
  ADICIONAR_PLACAR,
  ADICIONAR_JOGO_GABARITO,
  ALTERNAR_MODAL_EXCLUIR_USUARIO,
  ATUALIZAR_VERIFICARCAO,
  ADICIONAR_CONFIG
} from "./actions";

const initialState = {
  rodadaSelecionada: "1",
  jogos: {},
  jogosGabarito: {},
  config: {
    campeonato: { nome: "Bolo" },
    usuarioAdmin: {},
    atualizacaoAutomatica: false,
    bloquear: false
  },
  modalAberto: false,
  usuarioIdSelecionado: "",
  ultimaVerificacao: Date.now()
}

export default (state = initialState, action) => {
  switch (action.type) {
    case ADICIONAR_CONFIG:
      return { ...state, config: action.config };
    case ATUALIZAR_VERIFICARCAO:
      return { ...state, ultimaVerificacao: action.dezMinutosDepois };
    case ADICIONAR_JOGOS:
      return { ...state, jogos: action.jogos };
    case ADICIONAR_JOGO:
      return { ...state, jogos: { ...state.jogos, ...action.jogo } };
    case ADICIONAR_JOGO_GABARITO:
      return { ...state, jogosGabarito: { ...state.jogosGabarito, ...action.jogoGabarito } };
    case ADICIONAR_PLACAR: {
      const { id: jogoId, ...time } = action.time;
      return { ...state, jogosGabarito: { ...state.jogosGabarito, [jogoId]: { ...state.jogosGabarito[jogoId], ...time } } };
    }
    case RODADA_SELECIONADA:
      return { ...state, rodadaSelecionada: action.rodadaId }
    case ALTERNAR_MODAL_EXCLUIR_USUARIO: {
      if (action.usuarioId) {
        return { ...state, modalAberto: true, usuarioIdSelecionado: action.usuarioId }
      }
      return { ...state, modalAberto: false, usuarioIdSelecionado: "" }
    }
    default:
      return state
  }
}