
import { CARREGANDO_DESLIGADO } from "../carregando/actions";
export const MENSAGENS = "app/MENSAGENS";
export const LIMPAR_MENSAGENS = "app/LIMPAR_MENSAGENS";

export const snapshotToArray = querySnapshot => {
    let returnArr = [];
    querySnapshot.forEach(doc => {
        let item = { ...doc.data(), id: doc.id };
        returnArr.push(item);
    });
    return returnArr;
};

const adicionarMensagensFirebase = ({ message, code, name }) => {

    switch (code) {
        case "permission-denied":
            return {
                type: MENSAGENS,
                mensagens: [{ text: "Permisso Negada!", action: "Fechar" }]
            };
        case "resource-exhausted":
            return {
                type: MENSAGENS,
                mensagens: [{ text: "Cota diria de acesso ao servidor acabou!", action: "Fechar" }]
            };
        default:
            return {
                type: MENSAGENS,
                mensagens: [{ text: message, action: "Fechar" }]
            };
    }
};

export const adicionarMensagens = mensagens => dispatch => {
    console.log(mensagens);

    dispatch({ type: CARREGANDO_DESLIGADO });
    if (typeof mensagens === 'string' || mensagens instanceof String) {
        return dispatch({ type: MENSAGENS, mensagens: [{ text: mensagens, action: "Fechar" }] });
    }
    const { message, code, name } = mensagens;
    if (message && code && name) {
        return dispatch(adicionarMensagensFirebase(mensagens));
    }

    if (message) {
        return dispatch({ type: MENSAGENS, mensagens: [{ text: message, action: "Fechar" }] });
    }
};

export const limparMensagens = () => dispatch => dispatch({ type: LIMPAR_MENSAGENS })

