import { combineReducers } from "redux";
import usuario from "../usuario/reducers";
import seguranca from "../seguranca/reducers";
import palpite from "../palpite/reducers";
import admin from "../admin/reducers";
import simulador from "../simulador/reducers";
import carregando from "../carregando/reducers";

import { MENSAGENS, LIMPAR_MENSAGENS } from "./actions";

const initialState = {
  mensagens: []
}

const app = (state = initialState, action) => {
  switch (action.type) {
    case MENSAGENS:
      return { ...state, mensagens: [...action.mensagens] };
    case LIMPAR_MENSAGENS:
      return { ...state, mensagens: [] }
    default:
      return state
  }
}

export default combineReducers({
  usuario,
  seguranca,
  palpite,
  app,
  admin,
  simulador,
  carregando
});
