import React from "react";
import { TextField, Cell, Grid } from "react-md";
// import ESCUDOS from "app/escudos";
const Jogos = props => {

  return <Grid noSpacing={true}>
    {
      props.jogos.map((jogo, i) =>
        <Cell key={jogo.jogoId} phoneSize={4} desktopSize={6}>
          {/* <img alt={jogo.mandante_nome} style={{ width: "15%" }} src={ESCUDOS[jogo.mandante_nome]} /> */}
          <img alt={jogo.mandante_nome} style={{ width: "15%" }} src={jogo.mandante_escudo} />
          <TextField
            id={`${jogo.jogoId}_mandante`}
            type={props.exibirPlacar ? "number" : "text"}
            readOnly={!props.exibirPlacar}
            inputStyle={{ textAlign: "center" }}
            style={{ width: "25%" }}
            lineDirection="center"
            fullWidth={false}
            label={jogo.mandante_nome.substring(0, 10)}
            value={props.exibirPlacar || props.ehGabarito ? `${jogo.mandante_placar !== null ? jogo.mandante_placar : ""}` : "?"}
            onChange={props.exibirPlacar ? placar => props.quandoMudar({ id: jogo.id || jogo.jogoId, mandante_placar: parseInt(placar, 10) }) : _ => ""} />

          <span style={{ width: "5%" }}>{"  vs  "}</span>

          <TextField
            id={`${jogo.jogoId}_visitante`}
            type={props.exibirPlacar ? "number" : "text"}
            readOnly={!props.exibirPlacar}
            inputStyle={{ textAlign: "center" }}
            style={{ width: "25%" }}
            lineDirection="center"
            fullWidth={false}
            label={jogo.visitante_nome.substring(0, 10)}
            value={props.exibirPlacar || props.ehGabarito ? `${jogo.visitante_placar !== null ? jogo.visitante_placar : ""}` : "?"}
            onChange={props.exibirPlacar ? placar => props.quandoMudar({ id: jogo.id || jogo.jogoId, visitante_placar: parseInt(placar, 10) }) : _ => ""} />

          <span style={{ width: "5%" }}>{`  ${jogo.ponto || 0}  `}</span>

          {/* <img alt={jogo.visitante_nome} style={{ width: "15%" }} src={ESCUDOS[jogo.visitante_nome]} /> */}
          <img alt={jogo.visitante_nome} style={{ width: "15%" }} src={jogo.visitante_escudo} />
        </Cell>)
    }
  </Grid>
}

export default Jogos;