import React, { Component } from "react";
import { Button } from "react-md";
import { apagarCache } from "admin/actions";


export default class Erro extends Component {
  constructor(props) {
    super(props);
    this.state = { hasError: false };
  }

  componentDidCatch(error, info) {
    apagarCache();
    // Display fallback UI
    this.setState({ hasError: true });
    // You can also log the error to an error reporting service
    this.setState({ erros: { ...error, ...info } });
  }

  render() {
    if (this.state.hasError) {
      // You can render any custom fallback UI
      return (
        <div>
          <h1>Erro, contate o administrador: tire um print da tela e envie para reinaldovale@gmail.com.</h1>
          <div>{Object.keys(this.state.erros).map(error => <p>{this.state.erros[error]}</p>)}</div>
          <Button raised primary iconClassName="fa fa-hand-spock-o" onClick={e => this.setState({ hasError: false })}>OK</Button>
        </div>
      )
    }
    return this.props.children;
  }
}
