export const buscarJogos = (state) => {
  const { jogosSimulador: jogos, rodadaSelecionada } = state.simulador;
  return {
    jogos: Object
      .keys(jogos)
      .filter(jogoId => jogos[jogoId].rodada === rodadaSelecionada)
      .map(jogoId => jogos[jogoId])
      .sort((a, b) => a.mandante_nome > b.mandante_nome),
    rodadaSelecionada
  };
}