import React from "react";
import {
  Avatar,
  Grid,
  Card,
  CardTitle,
  CardText,
  DataTable,
  TableHeader,
  TableBody,
  TableRow,
  TableColumn,
  CardActions,
  Button,
  DialogContainer
} from "react-md";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { situacaoPreenchimento } from "./seletores";
import { ehAdmin } from "seguranca/actions";
import google from "../seguranca/google.svg";
import facebook from "../seguranca/facebook.svg";

import { removerUsuario } from "./actions";

const Situacao = ({ situacaoPreenchimento, removerUsuario, modalAberto, somenteAdmin }) => {

  return <Grid noSpacing={false}>
    {
      situacaoPreenchimento.map((usuario, i) => (
        <Card key={i} className="md-cell md-cell--12">
          <CardTitle
            title={usuario.nome}
            subtitle={
              usuario.conta === "google.com" ? <Avatar src={google} role="presentation" /> : <Avatar src={facebook} role="presentation" />
            }
            avatar={<Avatar src={usuario.foto} role="presentation" />}
          />
          {
            somenteAdmin &&
            <CardActions>
              <Button flat onClick={e => removerUsuario(false, usuario.uid)}>Remover Usurio</Button>
            </CardActions>
          }
          <CardText>
            <DataTable plain>
              <TableHeader>
                <TableRow>
                  <TableColumn>Rodada</TableColumn>
                  <TableColumn>Jogos Preenchidos</TableColumn>
                </TableRow>
              </TableHeader>
              <TableBody>
                {
                  Object.keys(usuario.rodadas).map((chave, i) =>
                    <TableRow key={i}>
                      <TableColumn>{chave}</TableColumn>
                      <TableColumn>{usuario.rodadas[chave]}</TableColumn>
                    </TableRow>
                  )
                }
              </TableBody>
            </DataTable>
          </CardText>
        </Card>
      ))
    }
    <DialogContainer
      id="simple-action-dialog"
      visible={modalAberto}
      onHide={e => removerUsuario(false)}
      actions={[<Button flat secondary onClick={e => removerUsuario(true)}>Confirmar</Button>]}
      title="Confirma Excluso?" />
  </Grid>
}

const mapStateToProps = state => {
  const modalAberto = state.admin.modalAberto;
  const somenteAdmin = ehAdmin(state);
  return { situacaoPreenchimento: situacaoPreenchimento(state), modalAberto, somenteAdmin };
};

const mapDispatchToProps = dispatch => bindActionCreators({
  removerUsuario
}, dispatch)

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Situacao))
