import { buscarJogos as buscarJogosSimulacao } from "../simulador/seletores";
export const classificacao = (state, rodadaSimulada) => {
  const usuarios = Object
    .keys(state.usuario.usuarios)
    .map(usuarioId => ({ ...state.usuario.usuarios[usuarioId], ...buscarJogos(state, usuarioId, undefined, rodadaSimulada) }))
    .sort((a, b) => (b.pontos - a.pontos) || (b.placares - a.placares) || (a.nome === b.nome ? 0 : a.nome < b.nome ? -1 : 1));

  let classificacao = 1;

  return usuarios.map((usuario, i) => {
    if (usuarios[i - 1]) {
      classificacao = usuarios[i - 1].pontos === usuario.pontos && usuarios[i - 1].placares === usuario.placares ? classificacao : ++classificacao;
    }
    return { ...usuario, classificacao };
  });
}

export const buscarJogos = (state, usuarioId, rodadaSelecionada, rodadaSimulada) => {
  // if (!rodadaSelecionada) {
  //   return {};
  // }
  const usuario = state.usuario.usuarios[usuarioId] || {};
  const todosPalpites = state.palpite.palpites;
  let jogosGabarito = Object.assign({}, { ...state.admin.jogos });

  if (rodadaSimulada) {
    buscarJogosSimulacao(state).jogos.forEach(jogo => jogosGabarito[jogo.jogoId] = jogo);
  }

  const jogos = todosPalpites
    .filter(palpite => rodadaSelecionada ? palpite.rodada === rodadaSelecionada && palpite.usuarioId === usuarioId : palpite.usuarioId === usuarioId)
    .map(palpite => (
      {
        ...jogosGabarito[palpite.jogoId],
        ...palpite,
        ...calcularPontos(jogosGabarito[palpite.jogoId], palpite)
      }))
    .sort((a, b) => ('' + a.mandante_nome).localeCompare(b.mandante_nome));

  const pontos = jogos.map(jogo => jogo.ponto).reduce((acumulado, atual) => acumulado + atual, 0);
  const placares = jogos.filter(jogo => jogo.ponto === 3).length;
  return {
    usuario,
    rodadaSelecionada,
    jogos,
    pontos,
    placares
  };
}

const calcularPontos = (gabarito, palpite) => {
  const gm = gabarito.mandante_placar,
    gv = gabarito.visitante_placar,
    pm = palpite.mandante_placar,
    pv = palpite.visitante_placar,
    vazio = gm === null || gv === null || pm === null || pv === null,
    gEmpate = gm === gv,
    pEmpate = pm === pv,
    tresPontos = gm === pm && gv === pv,
    umPonto = (gm > gv && pm > pv) || (gm < gv && pm < pv) || (gEmpate && pEmpate);

  if (vazio) return { ponto: 0 };
  if (tresPontos) return { ponto: 3 };
  if (umPonto) return { ponto: 1 };
  return { ponto: 0 };
}